<template>
    <div class="newworks">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 作业项目
                </el-breadcrumb-item>
                <el-breadcrumb-item>{{text}}</el-breadcrumb-item>
            </el-breadcrumb> 
        </div>
        <div class="container" v-loading="loading1">
            <div class="form">
                <el-form ref="form" label-width="110px">
                    <el-form-item label="作业名称："> 
                        <el-input size="small" v-model="name"></el-input>
                    </el-form-item>
                    <el-form-item label="简介：">
                        <el-input  size="small" type="textarea" rows="5" v-model="desc"></el-input>
                    </el-form-item>
                    <el-form-item label="资料：">
                      <div class="data-out">
                        <p class="data-bg" v-for="(item, index) in fileIdList">
                          <a target="_blank" :href="downurls+item.url"><span>{{item.name}}</span></a>
                          <i class="deldata el-icon-delete" @click="deldataone(0,index)"></i>
                        </p>
                        <el-button size="mini" @click="modifyStuList(0)">添加资料</el-button>
                      </div>
                    </el-form-item>
                    <el-form-item label="参考答案：">
                      <div class="data-out">
                        <p class="data-bg" v-for="(item, index) in answerList">
                          <a target="_blank" :href="downurls+item.url"><span>{{item.name}}</span></a>
                          <i class="deldata el-icon-delete" @click="deldataone(1,index)"></i>
                        </p>
                        <el-button size="mini" @click="modifyStuList(1)">添加资料</el-button>
                      </div>
                    </el-form-item>
                    <el-form-item>
                        <el-button size="small" type="primary" @click="savedata">保 存</el-button>
                        <!-- <el-button size="small" @click="editVisible = false">取 消</el-button> -->
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <!-- 编辑弹出框 -->
        <el-dialog title="添加资料" :visible.sync="editVisible" width="500px" v-loading="loading">
            <div class="select-bg">
                <span>选择文件夹：</span>
                <el-select v-model="value" @change="GetFileByFolderId" size="small" placeholder="请选择">
                    <el-option
                      v-for="item in options"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                </el-select>
            </div>
            <el-table
                size="mini"
                ref="multipleTable"
                class="editlist"
                :data="tableData"
                tooltip-effect="dark"
                height="300px"
                style="width: 100%"
                @selection-change="handleSelectionChange">
                <el-table-column
                  type="selection">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="全选">
                </el-table-column>
              </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="editVisible = false">取 消</el-button>
                <el-button size="small" type="primary" @click="savechoose">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import {fetchGetworkprojectlist, fetchGetallfolder, fetchGetFileByFolderId, fetchAddworkproject, fetchGetworkprojectinfo, fetchUpdateworkproject} from '../../axios/requests'
import {downurl} from '../../axios/index' 
export default {
    name: 'course', 
    data() {
        return {
            text:'新建作业项目',
            types:'',

            loading:false,
            loading1:false,
            editVisible:false,
            checked:false,
            tableData: [],
            options: [],

            value: '',
            fileIdList:[],
            answerList:[],
            chooselist:[],
            type:0,
            name:'',
            desc:'',
            downurls:'',
        };
    },
    mounted(){
      this.downurls = downurl;
      let {type} = this.$route.params;
      this.types = type;
      this.Getallfolder()
      if (type!=0) {
        this.Getworkprojectinfo(type);
        this.text="编辑作业项目"
      };
    },
    methods: {
        handleEdit(index, row) {
            console.log(index, row);
        },
        handleSelectionChange(e){
          // let data = e.map(d => d.id);
          this.chooselist = e;
        },
        savechoose(){
          if (this.type==0) {
            var newfileIdList=this.chooselist.concat(this.fileIdList)
            var arr2 = newfileIdList.filter((x, index,self)=>{
              var arrids = []
              newfileIdList.forEach((item,i) => {
                arrids.push(item.id)
              })
              return arrids.indexOf(x.id) === index
            }) 
            this.fileIdList = arr2
          }else{
            var newanswerList=this.chooselist.concat(this.answerList);
            var arr2 = newanswerList.filter((x, index,self)=>{
              var arrids = []
              newanswerList.forEach((item,i) => {
                arrids.push(item.id)
              })
              return arrids.indexOf(x.id) === index
            }) 
            this.answerList = arr2
          }
          this.chooselist=[]
          this.editVisible = false;
        },
        // 删除操作
        deldataone(type,index) {
            if (type==0) {
                  this.fileIdList.splice(index, 1);
                }else{
                  this.answerList.splice(index, 1);
                }
            // this.$confirm('确定要删除吗？', '提示', {
            //     type: 'warning'
            // })
            // .then(() => {
            //     // this.$message.success('删除成功');

                
            // })
            // .catch(() => {});
        },
        modifyStuList(type){
            this.GetFileByFolderId(this.value)
            this.editVisible = true;
            this.type=type;
        },

        savedata(){
          if (this.types==0) {
            this.Addworkproject()
          }else{
            this.Updateworkproject()
          }
        },

        async Updateworkproject(){//修改作业项目
          let {types, name, desc, fileIdList, answerList} = this;
          if (!name) {this.$message.error('作业名称不能为空');return};
          if (!desc) {this.$message.error('简介不能为空');return};
          if (fileIdList.length==0) {this.$message.error("资料不能为空");return};
          if (answerList.length==0) {this.$message.error("参考答案不能为空");return};
          let newfileIdList = String(fileIdList.map(d => d.id));
          let referenceSolutionList = String(answerList.map(d => d.id));
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          try {
            let response = await fetchUpdateworkproject(types, name, desc, newfileIdList, referenceSolutionList);
            if (response.status.code ==0) {
              this.$message({
                message: '修改成功',
                type: 'success'
              });
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            loading.close()
          }
        },

        async Addworkproject(){//新建作业项目
          let {name, desc, fileIdList, answerList} = this;
          if (!name) {this.$message.error('作业名称不能为空');return};
          if (!desc) {this.$message.error('简介不能为空');return};
          if (fileIdList.length==0) {this.$message.error("资料不能为空");return};
          if (answerList.length==0) {this.$message.error("参考答案不能为空");return};
          let newfileIdList = String(fileIdList.map(d => d.id));
          let referenceSolutionList = String(answerList.map(d => d.id));
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          try {
            let response = await fetchAddworkproject(name, desc, newfileIdList, referenceSolutionList);
            if (response.status.code ==0) {
              this.$message({
                message: '添加成功',
                type: 'success'
              });
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            loading.close()
          }
        },

        async GetFileByFolderId(folderId) {//得到文件夹下文件
          this.loading = true;
          try {
            let response = await fetchGetFileByFolderId(this.pageNum, this.pageSize, folderId);
            if (response.status.code ==0) {
              this.tableData = response.result.list;
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading = false;
          }
        },

        async Getworkprojectinfo(id) {//得到作业详情
          this.loading1 = true;
          try {
            let response = await fetchGetworkprojectinfo(id);
            if (response.status.code ==0) {
              this.name = response.result.name;
              this.desc = response.result.desc;
              this.fileIdList = response.result.filelist;
              this.answerList = response.result.referenceSolutionList;
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading1 = false;
          }
        },
        
        async Getallfolder() {//得到所有文件夹
          this.loading = true;
          try {
            let response = await fetchGetallfolder();
            if (response.status.code ==0) {
              this.options = response.result.list;
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading = false;
          }
        },
    }
};
</script>

<style lang='scss'>

</style>

<style lang='scss' scoped>
    .newworks{
      .crumbs{
          padding-left:20px;
        }
        .stu-list{
            border:1px solid #ccc;
        }
        .data-out{
          padding-top:10px;
        }
        .data-bg{
          font-size:12px;
          line-height:24px;
          color:#666;
          .deldata{
            cursor:pointer;
            margin-left:20px;
            color:#f56c6c;
            font-size:14px;
          }
        }
        .stunumber{
            padding:10px 0;
            color:#666;
            span{
                margin-right:40px;
            }
        }
        .editlist{
            margin-top:20px;
            border:1px solid #eee;
        }
    }
</style>


